
.exp-container{
  width: 100%;
  margin-top: 50px;
}
.exptitle{
  font-size: 50px;
  text-shadow: 2px 2px 5px #57AEEF;
  color: #CCD6F6;
  margin-bottom: 1.5em;
}
.cont{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.moodflow{
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 150px;
}
.textcont{
  flex:1;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.moodP{
  color: var(--main-bg-color);
  font-size: larger;
}
.bold{
  font-weight: bold;
  font-size: smaller;
}
.imgcont{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 30px;
  width: 50%;
}
.img{
  width: 23.2%;
  padding: 10px;
}
.img2{
  width: 69.5%;
  padding: 10px;
}

.realistic-marker-highlight4{
  position:relative;
  color:var(--main-bg-color);
}
.realistic-marker-highlight4::before{
  content:"";
  background-color:#C9975E;
  width:100%;
  height:1.5em;
  position:absolute;
  z-index:-1;
  filter:url(#marker-shape);
  left:-0.25em;
  top:0.1em;
  padding:0 0.25em;
}
.realistic-marker-highlight5{
  position:relative;
  color:var(--main-bg-color);
}
.realistic-marker-highlight5::before{
  content:"";
  background-color:var(--green);
  width:100%;
  height:1.5em;
  position:absolute;
  z-index:-1;
  filter:url(#marker-shape);
  left:-0.25em;
  top:0.1em;
  padding:0 0.25em;
}
.realistic-marker-highlight6{
  position:relative;
  color:var(--main-bg-color);
}
.realistic-marker-highlight6::before{
  content:"";
  background-color:var(--pink);
  width:100%;
  height:1.5em;
  position:absolute;
  z-index:-1;
  filter:url(#marker-shape);
  left:-0.25em;
  top:0.1em;
  padding:0 0.25em;
}
@media screen and (max-width: 1200px) {
  .moodflow{
    flex-direction: column;
    background-color: var(--txtcolor);
    padding: 5%;
    box-shadow: inset 0px 0px 10px 10px var(--main-bg-color);
  }
  .textcont, .imgcont {
    width: auto; 
  }
  .img{
    width: 33%;
    padding: 10px;
  }
  .img2{
    width: 95%;
    padding: 10px;
  }
  .moodP{
    font-size: 3vw;
  }
  .bold{
    font-size: 2.7vw;
  }
  .realistic-marker-highlight4::before, .realistic-marker-highlight5::before, .realistic-marker-highlight6::before{
    display: none;
  }
}
@media screen and (max-width: 400px){
  .imgcont{
    margin: 0;
  }
  .img{
    width: 33%;
    padding: 10px;
  }
  .img2{
    width: 95%;
    padding: 10px;
  }
}
